.cat-change__range-container {
  display: flex;
  position: relative;
  justify-content: center;
  min-width: 280px;
  width: 100%;
  margin: 0 auto;
  padding: 0;

  @media (min-width: $tablet-width) {
    // margin: 10px auto;
    margin: 0 0 18px;
  }

  @media (min-width: $desktop-width) {
    margin: 0 auto 10px;
  }
}

.cat-change__range-label {
  /*display: flex;
  justify-content: space-between;
    align-items: center;
  width: fit-content;*/

  @media (min-width: $tablet-width) {
    display: flex;
    flex-wrap: nowrap;
    width: 570px;
    justify-content: space-between;
    align-items: center;
    color: $black-color;
  }
}

.cat-change__range-button {
  @include uppercase();
  position: absolute;
  background-color: transparent;
  border: 0;
  cursor: pointer;
  font-family: "Oswald Regular", "Arial", sans-serif;
  font-size: 16px;
  line-height: 20px;
  margin: 0 auto;

  @media (min-width: $tablet-width) {
    position: relative;
    font-size: 20px;
    border-bottom: 1px dotted $black-color;
    padding-bottom: 6px;

    &:hover {
      transition: border-bottom 0.3s ease;
      border-bottom: 1px solid rgba($black-color, 0.4);
    }
  }

  @media (min-width: $desktop-width) {
    top: 3px;
  }
}

.button-before {
  left: 18%;

  @media (min-width: $tablet-width) {
    left: -12px;
  }

  // @media (min-width: $desktop-width) {
  //   left: -12px;
  // }
}

.button-after {
  right: 16.5%;

  @media (min-width: $tablet-width) {
    right: -10px;
  }

  // @media (min-width: $desktop-width) {
  //   right: -10px;
  // }
}

.cat-change__range {
  width: 73px;
  height: 12px;
  border: 1px solid $special_grey;
  background-color: $main-color;
  border-radius: 6px;

  @media (min-width: $tablet-width) {
    width: 428px;
    height: 6px;
    border: 0;
  }
}

/*-----------------------------Range-------------------------------*/

input[type="range"] {
  position: relative;
  appearance: none;
  background: $main-color;
  padding: 0 5px;

  @media (min-width: $tablet-width) {
    background-color: $special-range-gray;
    padding: 0;
  }
}

input[type="range"]:focus {
  outline: none;
}

input[type="range"]::-ms-track {
  width: 100%;
  cursor: pointer;
  background: transparent;
  border-color: transparent;
  color: transparent;
}

input[type="range"]::-webkit-slider-thumb {
  position: relative;
  appearance: none;
  height: inherit;
  width: 50%;
  background-color: $light-green;
  border-radius: 5px;

  @media (min-width: $tablet-width) {
    height: 34px;
    width: 34px;
    border-radius: 50%;
    background-color: $main-color;
    background-image: url("../img/icons/slider-control.png");
    background-position: 50% 30%;
    cursor: pointer;
    margin-top: -14px;
    margin-left: 0;
    box-shadow: 0 1px 3px $special_grey;

    &:hover {
      box-shadow: 0 3px 3px $special_grey;
      transition: box-shadow 0.2s;
    }

    &:active {
      background: url("../img/icons/slider-control--pressed.png");
      background-position: 50% 20%;
    }
  }
}

input[type="range"]::-webkit-slider-runnable-track {
  width: 100%;
  height: 6px;
  cursor: pointer;
  background: $main-color;
  border-radius: 5px;

  @media (min-width: $tablet-width) {
    background-color: $special-range-gray;
    padding: 0;
  }

  /*@media (min-width: $desktop-width) {
    height: 6px;
    cursor: pointer;
    background: $special-range-gray;
    border-radius: 5px;
  }*/
}

input[type="range"]:focus::-webkit-slider-runnable-track {
  background: $special-range-gray;
}

